@use '../../App/main.scss' as m;

.canvas {
  padding: 25px;

  padding-inline: 20px;
  max-height: 100%;
  max-width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  overflow: hidden;
}

.canvas__container {
  display: flex;
  position: relative;
  width: max-content;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.canvas__img {
  object-fit: contain;
  max-width: 100%;
  max-height: 100%;
  -webkit-user-drag: none;
}